@import url('https://fonts.googleapis.com/css?family=Nanum+Gothic|Nanum+Myeongjo');

.WhenWrapper {
	padding-top: 60px;
	padding-bottom: 100px;
}

.WhenTime {
	font-family: 'Nanum Myeongjo', serif;
	font-size: 20px;
	padding: 16px;
}

.WhenNameWrapper {
	display: inline;
	font-family: 'Nanum Myeongjo', serif;
	font-size: 20px;
	padding: 18px;
	margin: 0 auto;
	text-align: center;
}

.WhenName {
	display: inline-block;
	font-size: 20px;
}

.WhenNameAnd {
	display: inline-block;
	font-size: 14px;
	padding: 0px 6px;
}

.WhenText {
	font-family: 'Nanum Gothic', sans-serif;
	font-size: 15px;
	color: #666666;
	letter-spacing: 0.45px;
	line-height: 30px;
	padding: 16px;
	padding-top: 60px;
}