@import url('https://fonts.googleapis.com/css?family=Nanum+Gothic|Nanum+Myeongjo');

.WhereWraper {
	padding-top: 60px;
	padding-bottom: 0px;
	display: flex;
	align-items: center;
    justify-content: center;
}

.Where {
	width: 100%;
	max-width: 800px;
}

.WhereHeaderTitle {
	font-family: 'Nanum Myeongjo', serif;
	font-size: 20px;
	padding: 16px;
}

.WhereHeaderContent {
	color: #bbbbbb;
	font-family: 'Nanum Gothic', sans-serif;
	font-size: 15px;
	letter-spacing: 0.45px;
	line-height: 30px;
	padding: 16px;
}

.WhereDetail {
	color: #666666;
	font-family: 'Nanum Gothic', sans-serif;
	font-size: 15px;
	letter-spacing: 0.45px;
	line-height: 30px;
	padding: 16px;
}

.WhereMapWrapper {
	max-width: 100%;
}

.WhereTitle {
	font-weight: bold;
	text-align: left;
}

.WhereContent {
	text-align: left;
}