.HeaderWrapper {
	height: 500px;
	display: -webkit-flex;
	display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.Header {
	height: 100%;
	width: 100%;
	max-width: 500px;
	background-image: url(/invitation/static/media/title.989973d1.jpg);
	background-attachment: scroll;
	background-position-x: 50%;
	background-position-y: 50%;
	background-size: cover;
}

.HeaderBackground {
	height: 400px;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	background-color: black;
	background-size: cover;
	opacity: 0.4;
}

.HeaderTitle {
	position: relative;
	vertical-align: middle;
	font-family: EB Garamond, serif !important;
    font-weight: 400;
	font-size: 52px;
	color: white;
	text-align: center;
	letter-spacing: 0em;
	line-height: 1.15em;
	padding: 16px;
}
